import moment from "moment-jalaali";

export var datesChanges = {
    data() {
        return {}
    },
    methods: {
        persianDateToEn(date) {
            let dates = new Date(date);
            return moment(dates).local('en').format('YYYY-MM-DD');
        },
        enDateToPersian(date) {
            if (date) {
                return moment(date).locale('fa').format('HH:mm jYYYY/jMM/jDD');
            } else return null
        },
        changeIsoDate(time) {
            if(time){
                const date = new Date(time);
                return moment(date).locale('fa').format('jYYYY/jMM/jDD');
            } else return null

        },
        changeIsoTime(time) {
            const date = new Date(time);
            return moment(date).locale('fa').format('HH:mm');
        },
    }
}