<template>
  <div>
    <v-card elevation="0" :loading="loading" :disabled="loading">
      <BlockItem>
        <span v-if="!$route.params.id" slot="title">پست جدید</span>
        <span v-else slot="title">ویرایش پست</span>
        <template v-if="!$route.params.id" slot="icon">mdi-plus</template>
        <template v-else slot="icon">mdi-pencil</template>

        <v-form ref="form">
          <v-row class="pb-6">

            <v-col cols="12" class="pb-0" md="8">
              <v-text-field
                  type="text"
                  outlined
                  :rules="[$store.state.rules.required(data.title,'عنوان پست')]"
                  dense
                  color="txtf"
                  :disabled="loading"
                  v-model="data.title"
                  :loading="loading"
                  label="عنوان پست"
                  background-color="rgba(0,0,0,.01)"
              >
              </v-text-field>
            </v-col>


            <v-col cols="12" class="pt-0 pb-3 d-flex flex-wrap align-center add-post-checkbox" md="4">
              <v-checkbox
                  hide-details
                  dense
                  color="info"
                  v-model="data.comments_enabled"
                  class="mx-auto"
                  label="نظرات"
              >
              </v-checkbox>
              <v-checkbox
                  hide-details
                  dense
                  color="info"
                  class="mx-auto"
                  v-model="data.featured"
                  label="پست ویژه"
              >
              </v-checkbox>
              <v-checkbox
                  hide-details
                  dense
                  color="info"
                  class="mx-auto"
                  v-model="data.enabled"
                  label="فعال"
              >
              </v-checkbox>
            </v-col>


            <v-col cols="12" class="pb-0" md="4">
              <v-row class="mb-3 mb-md-0">
                <v-col cols="6" class="py-0" md="6">
                  <label style="cursor: pointer">
                    <input type="file" ref="file" accept=".jpeg,.jpg,.png" style="display: none" @change="onFileChange()">
                    <v-img v-if="bannerPreviewURL" width="100%" :src="bannerPreviewURL">
                      <p class="mb-1 up-blog-pic">تصویر نشان</p>
                      <v-icon color="white" class="up-blog-pic-icon">mdi-upload</v-icon>
                    </v-img>
                    <v-img v-else width="100%" src="../../assets/image/select-pic.jpg">
                      <p class="mb-1 up-blog-pic">تصویر نشان</p>
                      <v-icon color="white" class="up-blog-pic-icon">mdi-upload</v-icon>
                    </v-img>
                  </label>
                </v-col>
                <v-col cols="6" class="py-0" md="6">
                  <label style="cursor: pointer">
                    <input type="file" accept=".jpeg,.jpg,.png" ref="image" style="display: none" @change="onFileChangeImage()">
                    <v-img v-if="imagePreviewURL" width="100%" :src="imagePreviewURL">
                      <p class="mb-1 up-blog-pic">تصویر بلاگ</p>
                      <v-icon color="white" class="up-blog-pic-icon">mdi-upload</v-icon>
                    </v-img>
                    <v-img v-else width="100%" src="../../assets/image/select-pic.jpg">
                      <p class="mb-1 up-blog-pic">تصویر بلاگ</p>
                      <v-icon color="white" class="up-blog-pic-icon">mdi-upload</v-icon>
                    </v-img>
                  </label>
                </v-col>
              </v-row>
            </v-col>
            <v-col cols="12" class="py-0" md="8">
              <v-autocomplete
                  type="text"
                  outlined
                  color="txtf"
                  item-color="info"
                  :disabled="loading"
                  dense
                  multiple
                  item-text="title"
                  item-value="id"
                  :rules="[$store.state.rules.required(category,'دسته بندی')]"
                  :items="categories"
                  v-model="category"
                  :loading="loading"
                  label="دسته بندی ها"
                  background-color="rgba(0,0,0,.01)"
              >
              </v-autocomplete>
              <v-combobox multiple
                          color="txtf"
                          item-color="txtf"
                          v-model="tag"
                          label="برچسب ها"

                          append-icon=""
                          :rules="[rules.minArray(tag,'برچسب',0)]"
                          chips
                          dense
                          outlined
                          deletable-chips
                          :search-input.sync="search"
                          @keyup.tab="updateTags"
                          @paste="updateTags"
                          background-color="rgba(0,0,0,.01)"

              >
                <template v-slot:selection="{ attr, on, item, selected }">
                  <v-chip
                      style="height: 22px;font-size: 14px"
                      v-bind="attr"
                      :input-value="selected"
                      color="#2e2e2e"
                      class="white--text"
                      v-on="on"
                  >
                    <svg fill="#d7926b" width="14px" version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg"
                         xmlns:xlink="http://www.w3.org/1999/xlink"
                         viewBox="0 0 490 490" xml:space="preserve">
<path d="M64.333,490h58.401l33.878-137.69h122.259L245.39,490h58.401l33.878-137.69h119.92v-48.162h-108.24l29.2-117.324h79.04
	v-48.162H390.23L424.108,0H365.31l-33.878,138.661H208.79L242.668,0h-58.415l-33.864,138.661H32.411v48.162h106.298l-28.818,117.324
	h-77.48v48.162h65.8L64.333,490z M197.11,186.824h122.642l-29.2,117.324H168.292L197.11,186.824z"/>
</svg>
                    <span v-text="item" class="mr-1 mt-1"></span>
                  </v-chip>
                </template>
              </v-combobox>
            </v-col>
            <v-col cols="12" :class="[!data.article && errorCk ? 'error-ck' : '']" class="py-0">
              <ckeditor :editor="editor" v-model="data.article" :config="editorConfig"></ckeditor>

              <div class="v-text-field__details mt-2">
                <div class="v-messages theme--light error--text" role="alert">
                  <div class="v-messages__wrapper">
                    <v-slide-y-transition>
                      <div class="v-messages__message message-transition-enter-to" v-if="!data.article && errorCk">وارد
                        کردن متن پست الزامی است
                      </div>
                    </v-slide-y-transition>
                  </div>
                </div>
              </div>

            </v-col>

          </v-row>
        </v-form>
        <template v-slot:ltop>
          <v-btn v-if="$route.params.id && $store.state.permissions && $store.state.permissions['change-post']" @click="editPost()" height="32"
                 class="dash-btns dash-btns--blue"

                 dark>ویرایش پست
          </v-btn>

          <v-btn v-if="!$route.params.id && $store.state.permissions && $store.state.permissions['create-post']" @click="publish()" height="32"
                 class="dash-btns dash-btns--blue"
                 dark>انتشار پست
          </v-btn>
        </template>
      </BlockItem>
      <template v-slot:progress>
        <GDLoading/>
      </template>
    </v-card>
  </div>
</template>

<script>
import Vue from "vue";
import BlockItem from "@/components/Misc/BlockItem";
import CKEditor from '@ckeditor/ckeditor5-vue2';
import {datesChanges} from "@/components/Misc/datesChanges";
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import '@ckeditor/ckeditor5-build-classic/build/translations/fa';
import axios from "axios";
import GDLoading from "@/components/Dashboard/Misc/GDLoading";

Vue.use(CKEditor);

export default {
  name: "DashBlogAdd",
  created() {
    if (this.$route.params.id) {
      this.fetchPost();
    }
    this.fetchCategories();
  },
  components: {
    GDLoading,
    BlockItem,
  },
  mixins: [datesChanges],
  data() {
    return {
      rules: {
        minArray: (value, label, length) => {
          if (value) {
            if (value.length > length) {
              return true;
            }
          }
          return 'وارد کردن ' + label + ' الزامی است'
        },
      },
      errorCk: false,
      search: '',
      editor: ClassicEditor,
      editorConfig: {
        language: 'fa',
      },
      loading: false,
      data: {
        title: null,
        article: '',
        featured: false,
        enabled: true,
        comments_enabled: true,
      },
      category: [],
      tag: null,
      categories: [],
      banner: null,
      image: null,
      bannerPreviewURL: null,
      imagePreviewURL: null,

    }
  },
  methods: {
    updateTags() {
      this.$nextTick(() => {
        this.select.push(...this.search.split(","));
        this.$nextTick(() => {
          this.search = "";
        });
      });
    },
    formData() {
      let formData = new FormData();

      if (this.banner && (typeof this.banner === 'object')) {
        formData.append('banner', this.banner);
      }
      if (this.image && (typeof this.image === 'object')) {
        formData.append('image', this.image);
      }
      Object.keys(this.data).forEach(key => {
        formData.append(key, this.data[key]);
      })
      Object.keys(this.category).forEach((key) => {
        formData.append('category', this.category[key]);
      })
      Object.keys(this.tag).forEach((key) => {
        formData.append('tag', this.tag[key]);
      })
      return formData
    },
    publish() {

      this.$refs.form.validate();
      if (this.$refs.form.validate() && this.data.article) {
        this.loading = true;


        let formData = this.formData();

        axios.post(this.$store.state.host + '/blog/api/v1/post/create-post', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
            Accept: "*/*",
            Authorization: `Bearer ${this.$store.state.userId}`,
          }
        }).then(() => {
          this.notify('پست شما با موفقیت ثبت شد', 'success')
          this.$router.push({name: 'DashBlog'})
        }).catch(error => {
          this.errorHandle(error.response)
        }).finally(() => {
          this.loading = false;
        })
      } else {
        this.errorCk = true;
      }

    },
    editPost() {
      this.$refs.form.validate();
      if (this.$refs.form.validate() && this.data.article) {
        this.loading = true;

        let formData = this.formData();

        axios.patch(this.$store.state.host + '/blog/api/v1/post/change-post/' + this.$route.params.id, formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
            Accept: "*/*",
            Authorization: `Bearer ${this.$store.state.userId}`,
          }
        }).then(() => {
          this.notify('پست شما ویرایش شد', 'info')
          this.$router.push({name: 'DashBlog'})
        }).catch(error => {
          this.errorHandle(error.response)
        }).finally(() => {
          this.loading = false;
        })
      } else {
        this.errorCk = true;
      }
    },
    fetchPost() {
      this.loading = true;
      axios.get(this.$store.state.host + '/blog/api/v1/post/post-detail/' + this.$route.params.id).then(response => {
        this.data.article = response.data.data.article;
        this.bannerPreviewURL = response.data.data.banner;
        if (response.data.data.image) {
          this.image = response.data.data.image;
        }

        if (response.data.data.banner) {
          this.banner = response.data.data.banner;
        }

        this.imagePreviewURL = response.data.data.image;
        this.data.comments_enabled = response.data.data.comments_enabled;
        this.data.featured = response.data.data.featured;
        this.data.enabled = response.data.data.enabled;
        this.tag = response.data.data.tag;
        for (let item in response.data.data.category) {
          this.category.push(response.data.data.category[item].id)
        }


        this.data.title = response.data.data.title;
      }).catch(error => {
        this.errorHandle(error.response)
      }).finally(() => {
        this.loading = false;
      })
    },
    fetchCategories() {
      axios.get(this.$store.state.host + '/blog/api/v1/category/categories').then(response => {
        this.categories = response.data.data;
      }).catch(error => {
        this.errorHandle(error.response)
      })
    },
    onFileChange() {
      this.banner = this.$refs.file.files[0]; // use it in case of normal HTML input
      if(this.banner.size>2097152) {
        this.notify('حجم عکس بیشتر از 2 م.گ','error')
        return
      }
      if((this.banner.type!=='image/jpeg') && (this.banner.type!=='image/png')) {
        this.notify('فرمت غیر قابل پشتیبانی','error')
        return
      }
      //const file = payload; // in case vuetify file input
      if (this.banner) {
        this.bannerPreviewURL = URL.createObjectURL(this.banner);
        URL.revokeObjectURL(this.banner); // free memory
      } else {
        this.bannerPreviewURL = null
      }

    },
    onFileChangeImage() {
      this.image = this.$refs.image.files[0]; // use it in case of normal HTML input
      if(this.image.size>2097152) {
        this.notify('حجم عکس بیشتر از 2 م.گ','error')
        return
      }
      if((this.image.type!=='image/jpeg') && (this.image.type!=='image/png')) {
        this.notify('فرمت غیر قابل پشتیبانی','error')
        return
      }
      //const file = payload; // in case vuetify file input
      if (this.image) {
        this.imagePreviewURL = URL.createObjectURL(this.image);
        URL.revokeObjectURL(this.image); // free memory
      } else {
        this.imagePreviewURL = null
      }
    }
  }
}
</script>

